export default {
	data() {
		return {
			/* 当前标签 */
			tabCur: 0,
			/* 列表 */
			cartList: [{
				type: 1
			}, {
				type: 1
			}, {
				type: 0
			}, {
				type: 0
			}, {
				type: 0
			}, {
				type: 1
			}, {
				type: 1
			}],
		}
	},
	onReachBottom() {
		uni.showLoading({
			title: '加载中',
			mask: true
		})
		setTimeout(() => {
			uni.hideLoading()
		}, 1000);
	},
	onLoad(e) {
		this.tabCur = e.type
	},
	methods: {
		/* 标签选择 */
		tabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.swiperCur = this.tabCur
		},
		/* 页面跳转 */
		jump2item() {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		}
	}
}
